.btn-toggle {
  margin: 0 4rem;
  padding: 0;
  position: relative;
  border: none;
  height: 1.5rem;
  width: 3rem;
  border-radius: 1.5rem;
  color: #6b7381;
  background: #bdc1c8;
}

.btn-toggle:focus,
.btn-toggle.focus,
.btn-toggle:focus.active,
.btn-toggle.focus.active {
  outline: none;
}

.btn-toggle:before,
.btn-toggle:after {
  line-height: 1.5rem;
  width: 4rem;
  text-align: center;
  font-weight: 100;
  font-size: 0.75rem;

  position: absolute;
  bottom: 0;
  transition: opacity 0.25s;
  font-family: "gothambook", sans-serif;
  color: #000;
  /* font-size: 13px; */
  white-space: nowrap;
}

.btn-toggle:before {
  /* content: "Inactive"; */
  content: attr(data-inactive);
  left: -5.5rem;
}

.btn-toggle:after {
  /* content: "Active"; */
  content: attr(data-active);
  right: -4.5rem;
  opacity: 0.5;
}

.btn-toggle>.handle {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 1.125rem !important;
  background: #3da3e1;
  transition: left 0.25s;
}

.btn-toggle.active {
  transition: background-color 0.25s;
}

.btn-toggle.active>.handle {
  left: 1.6875rem;
  transition: left 0.25s;
}

.btn-toggle.active:before {
  opacity: 0.5;
}

.btn-toggle.active:after {
  opacity: 1;
}

#status-toggle button {
  height: 22px !important;
}

#status-toggle .btn-toggle>.handle {
  border-radius: 1.125rem !important;
  background: #3da3e1 !important;
}

#status-toggle .btn-toggle {
  border-radius: 1.5rem !important;
  background: #e8e8e8;
  box-shadow: none;
  margin: 0 5.5rem;
}

